export default {
    data() {
      return {
        resetInterval: null // 리소스 초기화를 위한 타이머
      };
    },
    mounted() {
      // 일정 시간 간격마다 리소스 초기화
      this.resetInterval = setInterval(() => {
        const observedElements = document.querySelectorAll('.fade-in');
        observedElements.forEach((el) => {
          el.classList.remove('active'); // 모든 요소 초기화
          setTimeout(() => {
            el.classList.add('fade-in'); // 초기 상태로 복구
          }, 50); // 짧은 지연 후 다시 설정
        });
      }, 30000); // 30초마다 실행 (필요에 따라 조정)
    },
    beforeDestroy() {
      if (this.resetInterval) {
        clearInterval(this.resetInterval); // 타이머 제거
      }
    }
  };