<template>
    <div>
      <div class="container">
        <div id="header">
          <div class="container">
            <div class="top">
              <div class="phnumber">
                <p>고객센터 : 010 - 2721 - 2084</p>
              </div>
              <div class="right">
                <!-- <ul>
                  <li><router-link to="/customer_service">고객 센터</router-link></li>
                </ul> -->
              </div>
            </div>
            <div class="top_main">
              <div class="header_logo">
                <router-link to="/">
                  <img src="@/assets/img/ONE_Cleaner.png" alt="상표">
                </router-link>
              </div>
              <div class="menu" ref="menu">
                <button class="hamburger" @click="toggleMenu" v-if="isMobile" aria-label="메뉴 버튼">
                  ☰
                </button>
                <div class="header_menu">
                  <ul v-if="isMenuOpen || !isMobile">
                    <li><router-link to="/home" @click.native="closeMenu">홈</router-link></li>
                    <li><router-link to="/company" @click.native="closeMenu">회사소개</router-link></li>
                    <li><router-link to="/cleaning" @click.native="closeMenu">클리닝</router-link></li>
                    <li><router-link to="/service" @click.native="closeMenu">부가서비스</router-link></li>
                    <!-- <li><router-link to="/board" @click.native="closeMenu">게시판</router-link></li> -->
                  </ul>
                </div>
              </div>
              <div class="inquire">
                <a href="">
                  <div id="kakao-talk-channel-chat-button" data-channel-public-id="_xbENbb" data-title="consult" data-size="small" data-color="yellow" data-shape="pc" data-support-multiple-densities="true"></div>
                </a>
              </div>
            </div>
          </div>
        </div>
        <div id="main">
          <router-view />
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        category_list: false,
        isMenuOpen: false, // 메뉴 열림/닫힘 상태
        isMobile: false, // 모바일 여부
        isDragging: false, // 드래그 상태
      };
    },
    methods: {
    toggleMenu() {
      if (this.isMobile) {
        this.isMenuOpen = !this.isMenuOpen;
      }
    },
    closeMenu() {
      if (this.isMobile) {
        this.isMenuOpen = false; // 메뉴 닫기
      }
    },
    checkMobile() {
      this.isMobile = window.innerWidth <= 480; // 화면 너비 확인
      if (!this.isMobile) {
        this.isMenuOpen = false; // 데스크톱일 경우 메뉴 자동 닫기
      }
    },
    handleOutsideClick(event) {
      const menu = this.$refs.menu;
      if (this.isMenuOpen && !menu.contains(event.target) && !this.isDragging) {
        this.closeMenu(); // 메뉴 외부를 클릭하면 닫기
      }
    },
    startDrag() {
      this.isDragging = true; // 드래그 시작
    },
    endDrag(event) {
      if (this.isDragging) {
        this.isDragging = false; // 드래그 종료
        this.handleOutsideClick(event); // 드래그 종료 후 메뉴 닫기 처리
      }
    },
  },
  mounted() {
    this.checkMobile(); // 초기 화면 크기 확인
    window.addEventListener("resize", this.checkMobile); // 화면 크기 변경 감지
    document.addEventListener("click", this.handleOutsideClick); // 외부 클릭 감지
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.checkMobile); // 이벤트 리스너 제거
    document.removeEventListener("click", this.handleOutsideClick); // 외부 클릭 감지 제거
  },
};
  </script>
  
  <style lang="scss">
