<template>
  <div class="snow_container">
    <canvas id="snow_canvas" ref="snowCanvas"></canvas>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ctx: null,
      canvas: null,
      W: 0, // 화면 너비
      H: 0, // 화면 높이
      particles: [],
      mp: 300, // 눈 입자 밀도
      angle: 0,
      animationFrameId: null,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.canvas = this.$refs.snowCanvas;

      if (this.canvas) {
        this.ctx = this.canvas.getContext("2d");

        // 초기 캔버스 크기 설정
        this.resizeCanvas();

        // 눈 입자 초기화
        this.initializeParticles();

        // 애니메이션 시작
        this.animate();

        // 창 크기 변경 시 캔버스 크기 업데이트
        window.addEventListener("resize", this.debouncedResize);
      } else {
        console.error("Canvas not found.");
      }
    });
  },
  beforeDestroy() {
    cancelAnimationFrame(this.animationFrameId);
    window.removeEventListener("resize", this.debouncedResize);
  },
  methods: {
    resizeCanvas() {
      const dpr = window.devicePixelRatio || 1; // 디스플레이 배율
      this.W = window.innerWidth;
      this.H = window.innerHeight;

      // 캔버스 크기를 디스플레이 배율에 맞춰 설정
      this.canvas.width = this.W * dpr;
      this.canvas.height = this.H * dpr;

      // 배율 적용
      this.ctx.scale(dpr, dpr);

      // 기존 입자 재배치
      this.particles.forEach((p) => {
        p.x = Math.random() * this.W;
        p.y = Math.random() * this.H;
      });
    },
    initializeParticles() {
      this.particles = Array.from({ length: this.mp }, () => ({
        x: Math.random() * this.W,
        y: Math.random() * this.H,
        r: Math.random() * 3 + 1, // 입자 반지름 (2 ~ 5px)
        speed: Math.random() * 0.5 + 0.3, // 눈 내리는 속도 (0.3 ~ 0.8)
        opacity: Math.random() * 1 + 0.6, // 불투명도 (0.3 ~ 1)
      }));
    },
    draw() {
      if (!this.ctx) return;

      // 캔버스 초기화
      this.ctx.clearRect(0, 0, this.W, this.H);

      // 눈 입자 그리기
      this.particles.forEach((p) => {
        this.ctx.globalAlpha = p.opacity; // 투명도 설정
        this.ctx.fillStyle = "rgba(255, 255, 255, 1)"; // 흰색
        this.ctx.beginPath();
        this.ctx.arc(p.x, p.y, p.r, 0, Math.PI * 2, true); // 원형 그리기
        this.ctx.fill();
      });

      // 입자 업데이트
      this.updateParticles();
    },
    updateParticles() {
      this.angle += 0.01;

      this.particles.forEach((p) => {
        // 속도에 따라 y축 이동
        p.y += p.speed;

        // 속도에 따라 x축 이동
        p.x += Math.sin(this.angle) * 0.3;

        // 화면을 벗어난 입자 재배치
        if (p.x > this.W + 5 || p.x < -5 || p.y > this.H) {
          p.x = Math.random() * this.W;
          p.y = -10;
        }
      });
    },
    animate() {
      this.draw();
      this.animationFrameId = requestAnimationFrame(this.animate);
    },
    debouncedResize: debounce(function () {
      this.resizeCanvas();
    }, 300),
  },
};

function debounce(func, wait) {
  let timeout;
  return function (...args) {
    const context = this;
    clearTimeout(timeout);
    timeout = setTimeout(() => func.apply(context, args), wait);
  };
}
</script>

<style lang="scss">
.snow_container {
  width: 100%;
  height: 100%;
  position: fixed; /* 고정된 위치 */
  top: 0;
  left: 0;
  pointer-events: none; /* 마우스 이벤트 차단 */
  z-index: 9999; /* 화면 상단 */
}

canvas {
  display: block;
  width: 100%;
  height: 100%;
}
</style>





