<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <!-- CarouselSlide 컴포넌트로 슬라이드 아이템 표시 -->
      <div
        class="swiper-slide"
        v-for="(slide, index) in slides"
        :key="index"
        :style="{ backgroundImage: 'url(' + require('@/assets/img/header_bg07.png') + ')' }"
      >
        <div class="flex">
          <div class="img1">
            <h2>{{ slide.title }}</h2>
            <h3>{{ slide.subtitle }}</h3>
            <h4></h4>
            <div><router-link to="/cleaning"  class="click_clean">CLICK</router-link></div>
          </div>
          <div class="main_bgtool">
            <!-- 배경 이미지가 있는 곳에 추가 콘텐츠를 삽입할 수 있음 -->
            <img :src="slide.image" alt="slide image" />
          </div>
        </div>
      </div>
    </div>

    <!-- Next, Prev Buttons -->
    <button @click="prev" class="main_btn prev"><img src="@/assets/img/arrow_left.png" alt=""></button>
    <button @click="next" class="main_btn next"><img src="@/assets/img/arrow_right.png" alt=""></button>
  </div>
</template>

<script>
import Swiper from "swiper/bundle";
import "swiper/swiper-bundle.css";

export default {
  data() {
    return {
      slides: [
        {
          image: require("@/assets/img/main_bg1.jpg"),
          title: "아파트 및 빌딩 청소",
          subtitle: "바닥 / 화장실",
        },
        {
          image: require("@/assets/img/main_bg2.jpg"),
          title: "빌라 및 원룸 청소",
          subtitle: "바닥 / 입구 유리 / 원룸",
        },
      ],
      swiperInstance: null, // Swiper 인스턴스를 저장할 변수
    };
  },
  methods: {
    prev() {
      if (this.swiperInstance) {
        this.swiperInstance.slidePrev();
      }
    },
    next() {
      if (this.swiperInstance) {
        this.swiperInstance.slideNext();
      }
    },
    initSwiper() {
      // Swiper 인스턴스 초기화
      this.swiperInstance = new Swiper(".swiper-container", {
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true, // 무한 루프
        autoplay: {
          delay: 5000, // 3초 간격으로 자동 슬라이드
          disableOnInteraction: false, // 사용자 상호작용 후에도 자동 슬라이드 계속
        },
        pagination: {
          el: ".swiper-pagination",
          type: "bullets",
          clickable: true,
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
        loopAdditionalSlides: 1, // 슬라이드 추가 로딩
        preloadImages: true, // 이미지를 미리 로드
      });
    },
  },
  mounted() {
    this.initSwiper(); // Swiper 초기화
  },
};
</script>

<style lang="scss">
.swiper-container {
  position: relative;
  width: 100%;
  height: 500px;
  overflow: hidden;
  margin-bottom: 50px;
  padding:170px 0;
}

.swiper-wrapper {
  display: flex;
}

.swiper-slide {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-size: cover;
  background-position: center;
  /* 배경 이미지가 제대로 보여지도록 추가 */
}

.swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.flex {
  width: 100%;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  gap: 200px;
}

.img1 {
  text-align: center;
}

.img1 h2 {
  font-size: 50px;
}

.img1 h3 {
  font-size: 36px;
  margin: 30px 0;
}

.img1 h4 {
  font-size: 28px;
}

.main_bgtool {
  width: 650px;
  height: 400px;
  overflow: hidden;
  border-radius: 70px;
}

.main_bgtool img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* 버튼 스타일 */
.main_btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  padding: 10px;
  z-index: 10;
  cursor: pointer;
}

.next {
  right: 10px;
}

.prev {
  left: 10px;
}

.swiper-button-prev,
.swiper-button-next {
  color: white;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  z-index: 10;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 0;
}

.swiper-button-prev {
  left: 10px;
}

.swiper-button-next {
  right: 10px;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  background-color: transparent !important;

}

.swiper-pagination {
  bottom: 10px;
}

.swiper-pagination-bullet {
  background-color: #007bff;
}


.main_btn img {
  width: 30px;
  padding: 20px;
}


.click_clean {
  border: 3px solid rgb(44, 44, 252);
  color: rgb(44, 44, 252);
  border-radius: 30px;
  padding: 10px 30px;
  font-weight: bold;
}
</style>
