import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './assets/scss/_reset.scss';
import './assets/scss/_common.scss';
import container from './components/mainheader.vue'
import kakao from "@/components/main_banner/right_banner.vue"
import footer from "@/components/mainfooter.vue";

// vue-meta
import VueMeta from "vue-meta";  // vue-meta 추가
Vue.use(VueMeta);  // vue-meta 사용 설정


// 믹스인(시간마다 리셋)
import ResetMixin from './components/js/resetMixin.js' // 경로를 실제 프로젝트 구조에 맞게 수정

Vue.mixin(ResetMixin); // 전역 믹스인 등록


Vue.config.productionTip = false

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')


new Vue({
    router,
    store,
    render: h => h(container)
}).$mount('#container')

new Vue({
    router,
    store,
    render: h => h(footer)
}).$mount('#footer')


new Vue({
    router,
    store,
    render: h => h(kakao)
}).$mount('#kakao')






