import Vue from 'vue'
import VueRouter from 'vue-router'



// import { createRouter, createWebHistory } from 'vue-router';
import Home from '@/components/top_menu/home.vue';
import '@/assets/reaction/laptop.scss'
import '@/assets/reaction/phone.scss'
import '@/assets/reaction/tablets.scss'


Vue.use(VueRouter)


// log, information, service_center (main)
const routes = [{
    path: '/login',
    name: 'login',
    component: () =>
        import ('@/components/menu/login.vue')
}, {
    path: '/cleaning',
    name: 'cleaning',
    component: () =>
        import ('@/components/top_menu/cleaning.vue')
}, {
    path: '/board',
    name: 'board',
    component: () =>
        import ('@/components/top_menu/board.vue')
}, {
    // top_menu (main)
    path: '/home',
    name: 'home',
    component: () =>
        import ('@/components/top_menu/home.vue')
}, {
    path: '/service',
    name: 'service',
    component: () =>
        import ('@/components/top_menu/service.vue')
}, {
    path: '/company',
    name: 'company',
    component: () =>
        import ('@/components/top_menu/company.vue')
},  {
    path: '/',
    name: 'Home',
    component: Home
}, {
    path: '/customer_service',
    name: 'customer_service',
    component: () =>
        import ('@/components/top_menu/cs.vue')
},

]



const router = new VueRouter({
    mode: 'hash', // 해시 모드 활성화
    base: process.env.BASE_URL,
    routes,
});

router.afterEach(() => {
    window.scrollTo(0, 0); // 페이지 이동 후 스크롤을 맨 위로 설정
});

// 새로고침 시에도 스크롤을 맨 위로 설정
window.onload = () => {
    window.scrollTo(0, 0); // 페이지 새로고침 후 스크롤을 맨 위로 설정
};


export default router